import roads from "../data/division/roads.json"

const getRoads = (circle, division, category) => (
  getRoadsFor(circle, division, category).sort((a, b) => a.road_number.localeCompare(b.road_number,undefined, {
    numeric: true,
    sensitivity: 'base'
  }))
)

const getRoadsFor = (circle, division, category) => {
  return roads.filter((road) => (road.circle === circle && road.division === division && road.category === category))
}

const getDivisions = (circle) => (
  getDivisionsForCircle(circle).map((division) => ({ division: division}))
)

const getDivisionsForCircle = (circle) => {
  const circleData = roads.filter((road) =>  road.circle === circle)
  return [...new Set(circleData.map(item => item.division))]
}

const getRoadCategories = () => (
  getCategories().map((category) => ({category: category}))
)

const getCategories = () => {
  return ["SH", "SHU", "MDR", "ODR", "SCR"]
}

export { getRoads, getDivisions, getRoadCategories  }
