import './Roads.css';
import React, { useEffect, useState } from "react"
import { Badge, Button, Card, Col, Container, Form, Row, Tab, Tabs } from "react-bootstrap"
import { Typeahead } from "react-bootstrap-typeahead"
import { useTitle } from "react-use"
import { APP_NAME } from "../constants"
import circles from "../data/circles.json"
import { log } from "../helpers"
import { getRoads, getDivisions, getRoadCategories } from "../service/roads"
import IconExternalLink from "./svg/external-link"

const Roads = () => {
  const [selectedCircle, setSelectedCircle] = useState(null);
  const [divisions, setDivisions] = useState([]);
  const [selectedDivision, setSelectedDivision] = useState(null)
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(null)
  const [roads, setRoads] = useState([])
  const [submitButton, setSubmitButton] = useState(true)

  useTitle(`Roads | ${APP_NAME}`)

  useEffect(() => {
    log(">>>>> side-effect")
    if (!selectedCircle) {
      const circle = circles[0]
      const divisions = getDivisions(circle)
      log(`divisions for ${circle}: ${divisions}`)
      const division = divisions[0]
      const categories = getRoadCategories()
      const category = categories[0]
      const roads = getRoads(circle, division, category)

      setSelectedCircle([circle])
      setDivisions(divisions)
      setSelectedDivision([division])
      setRoads(roads)

      // Road Category
      setCategories(categories)
      setSelectedCategory([category])
    }
    log("<<<<< side-effect")
  }, [selectedCircle])


  const clearCircle = () => {
    setDivisions([])
    setSelectedDivision([])
    setSelectedCircle([])
    setSubmitButton(false)
  }

  const clearDivision = () => {
    setSelectedDivision([])
    setSubmitButton(false)
  }

  const clearCategory = () => {
    setSelectedCategory([])
    setSubmitButton(false)
  }

  const handleSubmit = (event) => {
      log("handle form submit")
      const circle = selectedCircle[0]
      const division = selectedDivision[0].division
      const category = selectedCategory[0].category
      log(`get roads for : Circle:${circle}-Division:${division}-Category:${category}`)
      const roads = getRoads(circle, division, category)
      log(`roads: ${JSON.stringify(roads)}`)
      setRoads(roads)
      event.preventDefault()
   }


  const handleCircleChange = (values) => {
    log(`handle circle change, values: ${values}`)
    setSelectedCircle(values)
    if (values.length) {
      const [circle] = values
      const divisions = getDivisions(circle)
      log(`Divisions for ${circle}: ${divisions}`)
      setDivisions(divisions)
      setSelectedDivision([divisions[0]])
      setSubmitButton(true)
    }
  }

  const handleDivisionChange = (values) => {
    log(`handle division change, values: ${values}`)
    setSelectedDivision(values)
    setSubmitButton(true)
  }

  const handleCategoryChange = (values) => {
    log(`handle category change, values: ${values}`)
    setSelectedCategory(values)
    setSubmitButton(true)
  }

  const getBadgeClass = (road_category) => {
    if (road_category === "SH" || road_category === "SHU" ) {
      return "success"
    } else if (road_category === "MDR") {
      return "primary"
    } else if (road_category === "ODR") {
      return "danger"
    } else if (road_category === "SCR") {
      return "secondary"
    }
  }

  const renderRoads = () => (
    roads.map((road, idx) => (
      <Row key={idx} className="my-3">
        <Col md={{ span: 8, offset: 2 }}>
          <Card className="shadow-sm">
            <Card.Body>
              <Row className="fs-5">
                <Col className="road-number">
                  <Badge bg={getBadgeClass(road.category)}>{`${road.category} ${road.road_number}`}</Badge>
                  <a target="_blank" href={`https://osm.org/relation/${road.osm_relation_id}`}><IconExternalLink /></a>
                </Col>
              </Row>
              <Row className="my-3">
                <Col className="road-name">
                  {road.name_en}
                </Col>
              </Row>
              <Row className="my-3">
                <Col className="road-length">
                  Total Length: <b>{road.total_length} Km</b>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    ))
  )

  return (
    <Container>
      <Row className="my-3">
        <p>
          {APP_NAME}
        </p>
      </Row>
      <Row className="my-3">
        <Col>
          <Form onSubmit={handleSubmit}>
            <Row>
              <Col md={{ span: 2, offset: 1 }}>
                <Form.Group>
                  <Form.Label>Circle</Form.Label>
                  <Typeahead
                    id="circle-selector"
                    onChange={handleCircleChange}
                    options={circles}
                    placeholder="Select a circle"
                    selected={selectedCircle !== null ? selectedCircle : []}
                  />
                  <input className="close-button" type="reset" value="x"  onClick={() => clearCircle()}/>
                </Form.Group>
              </Col>
              <Col md={2}>
                <Form.Group>
                  <Form.Label>Division</Form.Label>
                  <Typeahead
                    id="division-selector"
                    onChange={handleDivisionChange}
                    labelKey={(option) => `${option.division}`}
                    options={divisions}
                    placeholder="Select a division"
                    selected={selectedDivision !== null ? selectedDivision : []}
                  />
                  <input className="close-button" type="reset" value="x"  onClick={() => clearDivision()}/>
                </Form.Group>
              </Col>
              <Col md={2}>
                <Form.Group>
                  <Form.Label>Road Category</Form.Label>
                  <Typeahead
                    id="type-selector"
                    onChange={handleCategoryChange}
                    labelKey={(option) => `${option.category}`}
                    options={categories}
                    placeholder="Select a category"
                    selected={selectedCategory !== null ? selectedCategory : []}
                  />
                  <input className="close-button" type="reset" value="x"  onClick={() => clearCategory()}/>
                </Form.Group>
              </Col>
              <Col md={2}>
                <Form.Group>
                  <Form.Label>&nbsp;</Form.Label>
                  <Form.Control as={Button} type="submit" disabled={!submitButton}>
                    Submit
                  </Form.Control>
                </Form.Group>
              </Col>
            </Row>
          </Form>
        </Col>
      </Row>

      {renderRoads()}

      <Row className="mb-2">
        <Col>&nbsp;</Col>
      </Row>
    </Container>
  )
}

export default Roads
